import firebase from "./firebase";


export const getTimbangan = (props) => {
  console.log("HELLO FIREBASE");
  firebase
      .database()
      .ref("kotaMas/timbangan1")
      .on("value", (snapshot) => {
        let data = snapshot.val();
        props("berat_asli", data.berat);
        console.log(data.berat);
      });
};