import {
  React,
  Link,
  ModalGlobal,
  setItem,
  reset,
  Panel,
  ToastNotification,
  PanelHeader,
  connect,
  Tabel,
  hideModal,
  showModal,
  postData,
  getItem,
} from "../../../components/helpers/library";

import {
  setCamera,
  showEditBarang,
  HeadEditDataBarang,
  FormTambahBarang,
  FIlterPencarianBarang,
} from "./Module";

import { getData, postData2 } from "../../../config/axios";
import { showModalPesanan } from "../../../actions/databarang_action";
import ModalPesanan from "./ModalPesanan";
import firebase from '../../../firebase';

class TambahBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      formEdit: false,
      loadingData: false,
      databarang: [],
      dataPesanan: [],
      formTambahBarang: false,
      fromModalPesanan: false,
      isLoadingPesanan: false,
      noPesanan: "",
      columns: [
        // {
        //   dataField: "kode_barang",
        //   text: "Kode Barang ",
        //   sort: true,
        // },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          sort: true
        },
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "kode_toko",
          text: "Kode Baki",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "berat",
          text: "Berat",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {

            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.showModalDuplikatBarang(row)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-copy"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.showModalTambahBarang = this.showModalTambahBarang.bind(this);
    this.tambahbarang = this.tambahbarang.bind(this);
    // this.getBarangKodeBaki = this.getBarangKodeBaki.bind(this);
  }

  componentDidMount() {
    // getData("barang/get/by-kode-baki/SEM-01").then((res) => {
    //   if (res.data.length === 0) {
    //     this.setState({ databarang: [] });
    //   } else {
    //     this.setState({ databarang: res.data });
    //   }
    //   this.setState({ isLoading: false });
    // });
  }
  tambahbarang(data) {
    // console.log(data);
    this.setState({ isLoading: true });

    let data_post = {
      id_barang_pesanan: "-",
      kode_group: data.kode_kategori,
      kode_dept: data.kode_jenis,
      kode_gudang: data.kode_gudang,
      kode_toko: data.kode_lokasi_toko,
      kode_intern: data.kode_intern || "-",
      nama_barang: data.nama_barang,
      berat: parseFloat(data.berat),
      berat_asli: parseFloat(data.berat_asli),
      kadar_modal: parseFloat(data.kadar_modal) || 0,
      kadar: parseFloat(data.kadar),
      kadar_cetak: parseFloat(data.kadar_cetak),
      nama_atribut: data.nama_atribut || "-",
      berat_atribut: parseFloat(data.berat_atribut) || 0,
      harga_atribut: parseInt(data.harga_atribut) || 0,
      tag_id: "-",
      berat_bandrol: 0,
      no_pesanan: data.no_pesanan || "-",
    };

    // console.log(data_post);
    // console.log(saveImage);
    // return false;
    setTimeout(() => {
      postData2("tambah-barang/create", data_post)
        .then((res) => {
          let saveImage = {
            kode_barcode: res.data.kode_barcode,
            dataimage: data.foto,
          };
          if (data.foto === undefined) {
          } else {
            postData("imagebarang/add", saveImage)
              .then((res) => {
                console.log(res.data);
                ToastNotification(
                  "success",
                  "Data Barang Berhasil Di Tambahkan"
                );
              })
              .catch((error) => {
                console.log(error.response === undefined ? "Terjadi Kesalahan Saat Mengirim Data" : error.response.data);
                // ToastNotification('info', )
              });
          }
          getData("barang/get/by-kode-baki/" + data.kode_lokasi_toko)
            .then((res) => {
              if (res.data.length === 0) {
                this.setState({ databarang: [] });
              } else {
                this.setState({ databarang: res.data });
              }
              this.setState({ isLoading: false });
            })
            .catch((err) => {
              this.setState({ isLoading: false, databarang: [] });
              ToastNotification("info", "Kode Baki Yang Anda Dicari Tidak Ada");
            });
          this.props.dispatch(reset("FormTambahBarang"));
          firebase.database()
            .ref('kotaMas/timbangan1/')
            .set({ berat : 0, uid : "timbangan1" })
            .then(() => console.log('Data set.'))
          this.props.dispatch(hideModal());
          ToastNotification("success", "Data Barang Berhasil Di Tambahkan");
        })
        .catch((err) => {
          this.setState({ isLoading: false });
          ToastNotification(
            "info",
            err.response.data.message || "Gagal Tambah Barang Silahkan Coba Lagi"
          );
        });
    }, 300);
  }

  showModalDuplikatBarang(row) {
    getData("imagebarang/get/by-kode-barcode/" + row.kode_barcode)
      .then((res) => {
        let dataEdit = {
          foto: res.data.dataimage,
          kode_barcode: row.kode_barcode,
          nama_barang: row.nama_barang,
          nama_atribut: row.nama_atribut || "-",
          harga_atribut: row.harga_atribut || 0,
          kode_intern: row.kode_intern,
          kadar: row.kadar || 0,
          kadar_cetak: row.kadar_cetak || 0,
          kadar_modal: row.kadar_modal || 0,
          kode_kategori: row.kode_group,
          kode_jenis: row.kode_dept,
          kode_lokasi_toko: row.kode_toko + "~" + row.kode_gudang,
          berat: row.berat,
          berat_asli: row.berat_asli,
          berat_atribut: row.berat_atribut || 0,
          tag_id: row.tag_id,
          berat_bandrol: 0,
        };
        console.log(dataEdit);
        setItem("data_kategori_jenis", dataEdit);
        this.setState({
          formTambahBarang: true,
        });
        this.props.dispatch(showModal());
        this.props.dispatch(showEditBarang(dataEdit));
      })
      .catch((err) => {
        let dataEdit = {
          foto: '-',
          kode_barcode: row.kode_barcode,
          nama_barang: row.nama_barang,
          nama_atribut: row.nama_atribut || "-",
          harga_atribut: row.harga_atribut || 0,
          kode_intern: row.kode_intern,
          kadar: row.kadar || 0,
          kadar_cetak: row.kadar_cetak || 0,
          kadar_modal: row.kadar_modal || 0,
          kode_kategori: row.kode_group,
          kode_jenis: row.kode_dept,
          kode_lokasi_toko: row.kode_toko + "~" + row.kode_gudang,
          berat: row.berat,
          berat_asli: row.berat_asli,
          berat_atribut: row.berat_atribut || 0,
          tag_id: row.tag_id,
          berat_bandrol: 0,
        };
        setItem("data_kategori_jenis", dataEdit);
        this.setState({
          formTambahBarang: true,
        });
        this.props.dispatch(showModal());
        this.props.dispatch(showEditBarang(dataEdit));
      });

  }

  showModalTambahBarang(data) {
    // console.log(data);
    setItem("data_kategori_jenis", data);
    this.setState({
      formTambahBarang: true,
    });
    this.props.dispatch(showModal());
    this.props.dispatch(showEditBarang(data));
  }

  showBarangPesanan = () => {
    this.setState({
      fromModalPesanan: true,
      formTambahBarang: false,
    });
    this.props.dispatch(showModal());
    this.props.dispatch(showModalPesanan());
  };

  getBarangPesanan() {
    getData("pesanan/no/" + this.state.noPesanan)
      .then((res) => console.log(res.data))
      .catch((err) => console.log(err));
  }

  handleSubmit(data) {
    this.setState({ isLoading: true });
    let saveImage = {
      kode_barcode: data.kode_barcode,
      dataimage: data.foto,
    };
    // console.log(data.foto);
    let hasil = {
      nama_barang: data.nama_barang,
      nama_atribut: data.nama_atribut,
      harga_atribut: data.harga_atribut,
      kode_intern: data.kode_intern,
      kadar: data.kadar,
      kadar_cetak: data.kadar_cetak,
    };
    postData("barang/edit/" + data.kode_barcode, hasil)
      .then(() => {
        this.props.dispatch(hideModal());
      })
      .then(() => {
        if (data.foto === undefined) {
        } else {
          postData("imagebarang/add", saveImage)
            .then((res) => {
              // console.log(res.data);
            })
            .catch((error) => {
              // console.log(error.response.data);
            });
        }
      })
      .then(() => {
        this.setState({ databarang: [] });
        this.props.dispatch(reset("HeadEditDataBarang"));
        this.props.dispatch(setCamera(false));
      })
      .then(() => {
        ToastNotification("success", "Data Berhasil Disimpan");
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .catch((error) => {
        this.setState({ isLoading: false });
        ToastNotification("info", "Barang Gagal Di Edit");
      });
  }

  getNomorPesanan(nomor_pesanan) {
    this.setState({
      isLoadingPesanan: true
    })
    postData2("pesanan/valid/no", { no_pesanan: nomor_pesanan.value.trim() })
      .then((res) => {
        // console.log(res.data);
        this.setState({
          isLoadingPesanan: false,
        })
        let kode_toko = getItem('kode_lokasi_toko')
        let kode_jenis = getItem('kode_jenis')
        let kode_group = getItem('kode_group')
        let dataEdit = []

        res.data.detail_barang.forEach((detail) => {
          if (detail.kode_barcode === "-") {
            dataEdit = {
              kode_barcode: detail.kode_barcode,
              nama_atribut: detail.attribut || "-",
              harga_atribut: detail.harga_atribut || 0,
              kode_intern: "-",
              kadar: detail.perkiraan_kadar,
              nama_barang: detail.keterangan,
              kadar_cetak: 0,
              kadar_modal: detail.kadar_modal || 0,
              kode_kategori: kode_group,
              kode_jenis: kode_jenis,
              kode_lokasi_toko: kode_toko,
              berat: detail.perkiraan_berat,
              berat_asli: detail.perkiraan_berat || 0,
              berat_atribut: detail.berat_atribut || 0,
              tag_id: detail.tag_id || "-",
              berat_bandrol: 0,
              no_pesanan: nomor_pesanan.value,
              status: "PESANAN",
            };
          } else {
            getData("imagebarang/get/by-kode-barcode/" + detail.kode_barcode)
              .then((res) => {
                this.setState({
                  isLoadingPesanan: false,
                })
                dataEdit = {
                  foto: res.data.dataimage || "",
                  kode_barcode: detail.kode_barcode,
                  nama_atribut: detail.attribut || "-",
                  harga_atribut: detail.harga_atribut || 0,
                  kode_intern: "-",
                  kadar: detail.perkiraan_kadar,
                  nama_barang: detail.keterangan,
                  kadar_cetak: 0,
                  kadar_modal: detail.kadar_modal || 0,
                  kode_kategori: kode_group,
                  kode_jenis: kode_jenis,
                  kode_lokasi_toko: kode_toko,
                  berat: detail.perkiraan_berat,
                  berat_asli: detail.perkiraan_berat || 0,
                  berat_atribut: detail.berat_atribut || 0,
                  tag_id: detail.tag_id || "-",
                  berat_bandrol: 0,
                  no_pesanan: nomor_pesanan.value,
                  status: "PESANAN",
                };
              })
              .catch((err) => {
                this.setState({
                  isLoadingPesanan: false,
                })
                dataEdit = {
                  kode_barcode: detail.kode_barcode,
                  nama_atribut: detail.attribut || "-",
                  harga_atribut: detail.harga_atribut || 0,
                  kode_intern: "-",
                  kadar: detail.perkiraan_kadar,
                  kadar_cetak: 0,
                  kadar_modal: detail.kadar_modal || 0,
                  kode_kategori: kode_group,
                  nama_barang: detail.keterangan,
                  kode_jenis: kode_jenis,
                  kode_lokasi_toko: kode_toko,
                  berat: detail.perkiraan_berat,
                  berat_asli: detail.perkiraan_berat || 0,
                  berat_atribut: detail.berat_atribut || 0,
                  tag_id: detail.tag_id || "-",
                  berat_bandrol: 0,
                  no_pesanan: nomor_pesanan.value,
                  status: "PESANAN",
                };
              });
          }
        });
        this.props.dispatch(showModal());
        this.props.dispatch(showEditBarang(dataEdit));
        this.setState({
          formTambahBarang: true,
          fromModalPesanan: false,
        });

      })
      .catch((err) => {
        // console.log(err);
        this.setState({
          isLoadingPesanan: false,
        })
        ToastNotification(
          "info",
          err.response === undefined
            ? "Terjadi Kesalahan Saat Request Data"
            : err.response.data.message
        );
      });
  }

  render() {
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Nama Atribut : {row.nama_atribut} <br />
              Betat Atribut : {row.berat_atribut} <br />
              Harga Atribut : {row.harga_atribut} <br />
              Kode Intern : {row.kode_intern} <br />
              Kadar : {row.kadar} <br />
              Kadar Cetak : {row.kadar_cetak} <br />
              Kode Toko : {row.kode_toko} <br />
              Kode Gudang : {row.kode_gudang} <br />
              Stock : {row.stock_on_hand} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Barang</Link>
          </li>
          <li className="breadcrumb-item active">Tambah Barang</li>
        </ol>
        <h1 className="page-header">Tambah Barang </h1>

        <Panel>
          <PanelHeader>Tambah Data Barang</PanelHeader>
          <br />
          <div className="container">
            <FIlterPencarianBarang
              onSubmit={(data) => this.showModalTambahBarang(data)}
              barangPesanan={this.showBarangPesanan}
            />
          </div>
        </Panel>
        <Panel>
          <PanelHeader>Data Barang</PanelHeader>
          <br />
          <div className="container">
            <Tabel
              keyField="kode_barang"
              exportCsv={true}
              data={this.state.databarang}
              columns={this.state.columns}
              empty={true}
              textEmpty={
                this.state.loadingData ? (
                  <>
                    {" "}
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari Data Barang
                  </>
                ) : (
                  "Data Barang Kosong"
                )
              }
              expandRow={expandRow}
            />
          </div>
          <br />
          {/* End Data Barang */}
          <ModalGlobal
            size="l"
            title={
              this.state.formTambahBarang
                ? "Tambah Barang"
                : this.state.fromModalPesanan
                  ? "Barang Pesanan"
                  : "Edit Barang"
            }
            content={
              this.state.formTambahBarang ? (
                <FormTambahBarang
                  isLoading={this.state.isLoading}
                  onSubmit={(data) => this.tambahbarang(data)}
                />
              ) : this.state.fromModalPesanan ? (
                <ModalPesanan
                  isLoadingPesanan={this.state.isLoadingPesanan}
                  getNomorPesanan={(no) => this.getNomorPesanan(no)}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              ) : (
                <HeadEditDataBarang
                  isLoading={this.state.isLoading}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              )
            }
          />
          {/* End Tambah Data Barang  */}
        </Panel>
      </div>
    );
  }
}

export default connect()(TambahBarang);
